.banner{
    background-image: url("../../assets/menuBackground.png");
    height: 9rem;
    margin-top: 5rem;
    div{
        width: 70%;
        margin: auto;
        padding: 2rem;
        span{
            color: var(--white-color);
        }
        h3{
            font-weight: bold;
            color: var(--white-color);
            font-size: 2rem;
            margin: .5rem;
            text-transform: uppercase;
        }
    }
}